import React, {useState} from 'react';
import {Link} from 'react-router-dom'
import { BiMenuAltRight } from 'react-icons/bi';

const Nav = () => {
    const [menuStatus, setMenuStatus] = useState(false);
    return(
        <div className="nav">
            <div className={`menuWrap ${menuStatus ? 'active-menu' : ''}`}>
                <div className="menu">
                        <h1>Személyes információk</h1>
                    <h1>Tanulmányaim</h1>
                    <h1>Bizonyítványaim</h1>
                    <h1>Digitális készségeim</h1>
                    <h1>Szabadidő</h1>
                </div>
            </div>
            <div className="menuIconDiv">
                <BiMenuAltRight className={`menuIcon ${menuStatus ? 'active-Icon' : ''}`} onClick={() => setMenuStatus(!menuStatus)} />
            </div>
        </div>
    );
}

export default Nav;